/* contact */
.contact-font {
  color: #3f57ff;
  font-weight: 500;
}
.link {
  text-decoration: none;
  line-height: 2;
  color: #3f57ff;
}
.link:hover {
  text-decoration: none;
  color: #374eff;
}
.contact {
  line-height: 1;
  color: #3f57ff;
  text-decoration: none;
}
.contact:hover {
  color: #374eff;
  text-decoration: none;
}
.contact-btn {
  border-radius: 10px;
  color: white;
  background: #3f57ff;
}
.contact-btn:hover {
  color: white;
  background: #374eff;
}
.contact-icon {
  color: #3f57ff;
}

/* footer */
.footer-bg {
  background: #3f57ff;
}

/* model form */
.form-border {
  border-radius: 15px;
}
.form-bg {
  /* background: #d0dfe8; */
  background: rgb(216, 213, 213);
}
.form-btn {
  border-radius: 10px;
}
.modal-title {
  color: rgb(99, 97, 97);
  font-weight: 600;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.4);
}
::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.9);
}
